.footer {
    width: 100%;
    height: 100%;
    background: #407BFF;

}

.footer_text h1 {
    text-align: center;
    padding: 10px;
}

.footer_text p {
    text-align: center;
    padding: 10px;
}


.contactCircles {
    display: flex;
    align-items: center;
    justify-content: center;
}

.contactCircles li {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    background: #1f2235;
    margin: 10px 10px;
    cursor: pointer;
}

.contactCircles li a {
    color: #fff;
}

.contactCircles li:hover {
    background: #fff;
    transition: all 0.3s linear;
}

.contactCircles li a:hover {
    color: #407BFF;
}